/**
 * Product -> Ezeep Group directive
 */
export default angular.module('deskworks.ezeepGroupDirective', [
  'deskworks.EzeepUser',
  'deskworks.EzeepGroup',
  'deskworks.EzeepSettings',
])

  .directive('ezeepGroup', function() {
    const templateUrl = 'group/ezeep-group.tpl.html';
    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'EzeepGroupCtrl as $ctrl',
      bindToController: {
        userId: '<',
        save: '=',
      },
    };
  })

  .controller('EzeepGroupCtrl', function($q, dwAlerts, helper, state, EzeepUser, EzeepGroup, EzeepSettings) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      $ctrl.load();
    };

    /**
     * Load data
     */
    $ctrl.load = () => {
      return EzeepSettings.get()
        .catch(err => dwAlerts.error(err, 'Failed to query Ezeep settings.'))
        .then(ezeepSettings => {
          $ctrl.ezeepEnabled = ezeepSettings.enabled;
          const promises = {};

          if ($ctrl.ezeepEnabled) {
            promises.ezeepGroups = EzeepGroup.query()
              .catch(err => dwAlerts.error(err, 'Failed to query Ezeep groups.'));
          }

          if ($ctrl.userId && $ctrl.userId !== 'new' && !helper.isUid($ctrl.userId)) {
            promises.ezeepUser = EzeepUser.get($ctrl.userId)
              .catch(err => dwAlerts.error(err, 'Failed to get Ezeep user.'));
          }

          return $q.all(promises)
            .then(result => {
              if (result.ezeepUser) {
                $ctrl.ezeepUser = result.ezeepUser;
              } else {
                $ctrl.ezeepUser = { ezeepGroupId: ezeepSettings.defaultEzeepGroupId };
              }

              if ($ctrl.ezeepEnabled) {
                $ctrl.ezeepGroups = result.ezeepGroups;
              } else {
                $ctrl.ezeepGroups = [];
              }

              // expose save method only if have Groups and Ezeep enabled
              if ($ctrl.ezeepGroups && $ctrl.ezeepGroups.length && $ctrl.ezeepEnabled) $ctrl.save = save;
            });
        });
    };

    /**
     * Save Ezeep user
     */
    function save() {
      if (!$ctrl.ezeepEnabled) return;

      $ctrl.ezeepUser.id = $ctrl.userId;
      return EzeepUser.save($ctrl.ezeepUser)
        .catch(err => dwAlerts.error(err, 'Failed to save Ezeep user.'));
    }
  });
