export default angular.module('deskworks.EzeepUser', [
  'ngResource',
  'deskworks.config',
])

  .factory('EzeepUser', function($resource, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/ezeep/users/:userId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      get: (userId) => res.get({ userId }).$promise,

      save: (data) => res.update({ userId: data.id }, data).$promise,
    };
  });
