export default angular.module('deskworks.EzeepErrors', [
  'ngResource',
  'deskworks.config',
  'datesDeserializer',
])

  .factory ('EzeepErrors', function($resource, deskworksConfig, datesDeserializer) {

    const desDates = datesDeserializer(['errors.time']);

    const res = $resource(deskworksConfig.getApiAbsUrl('/ezeep/errors'), {}, {
      query: { method: 'GET', isArray: false, transformResponse: [angular.fromJson, desDates] },
    });

    return {
      query: (page, pageSize) => {
        return res.query({ page, pageSize }).$promise;
      },
    };
  });
