export default angular.module('deskworks.EzeepGroup', [
  'ngResource',
  'deskworks.config',
])

  .factory('EzeepGroup', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/ezeep/ezeep-groups'));

    return {
      query: placeId => {
        return res.query({ placeId }).$promise
          .then(groups => {
            groups.unshift({ id: null, name: 'No Access' });
            return groups;
          });
      },
    };
  });
