import niceTimeDirective from 'shared/nice-date-time/nice-time.directive';

export default angular.module('deskworks.EzeepErrorsCtrl', [
  'deskworks.dwAlerts',
  'deskworks.EzeepErrors',
  niceTimeDirective.name,
])

  .controller('EzeepErrorsCtrl', function($scope, $translate, state, dwAlerts, EzeepErrors) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('EZEEP_ERRORS.PAGE_TITLE'));

    /**
     * Fetch log data from server
     */
    $ctrl.fetch = page => {
      $ctrl.fetching = true;
      return EzeepErrors.query(page || $ctrl.data && $ctrl.data.page || 1, 20)
        .catch(err => dwAlerts.error(err, 'Failed to query Ezeep errors.'))
        .then(result => {
          $ctrl.data = result;
        })
        .finally(() => { $ctrl.fetching = false });
    };

    /**
     * Start initial fetching
     */
    $ctrl.fetch();
  });
