export default angular.module('deskworks.EzeepSettings', [
  'ngResource',
  'deskworks.config',
])

  .factory('EzeepSettings', function($resource, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/ezeep/settings'), {}, {
      get: { method: 'GET' },
      update: { method: 'PUT' },
    });

    return {
      get: () => res.get().$promise,

      save: data => res.update({}, data).$promise,
    };
  });
