/**
 * Integrations Setup -> Ezeep Settings directive
 */
export default angular.module('deskworks.ezeepSettingsDirective', [
  'debounce',
  'deskworks.EzeepGroup',
  'deskworks.EzeepSettings',
])

  .directive('ezeepSettings', function() {
    const templateUrl = 'settings/ezeep-settings.tpl.html';
    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'EzeepSettingsCtrl as $ctrl',
    };
  })

  .controller('EzeepSettingsCtrl', function(dwAlerts, debounce, EzeepGroup, EzeepSettings) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      $ctrl.load();
    };

    /**
     * Load settings
     */
    $ctrl.load = () => {
      $ctrl.spin = true;

      return EzeepSettings.get()
        .catch(err => dwAlerts.error(err, 'Failed to load Ezeep settings.'))
        .finally(() => { $ctrl.spin = false; })
        .then(settings => { $ctrl.settings = settings; })
        .then(result => { if ($ctrl.settings && $ctrl.settings.enabled) return loadEzeepGroups(); });
    };

    /**
     * Save settings
     */
    $ctrl.save = debounce(save, 1000);
    function save(form) {
      if (form.$invalid) return;
      $ctrl.spin = true;
      return EzeepSettings.save($ctrl.settings)
        .catch(err => dwAlerts.error(err, 'Failed to save Ezeep settings.'))
        .finally(() => { $ctrl.spin = false; });
    };

    /**
     * Change Ezeep Enabled handler
     */
    $ctrl.changeEnabled = (form) => {
      if ($ctrl.settings.enabled) {
        $ctrl.save(form)
          .then(() => { return loadEzeepGroups(); });
      } else {
        return save(form);
      }
    };

    function loadEzeepGroups() {
      $ctrl.$$ezeepGroups = null;

      $ctrl.$$groupSpin = true;
      return EzeepGroup.query()
            .catch(err => dwAlerts.error(err, 'Failed to query Ezeep groups.'))
            .then(ezeepGroups => { $ctrl.$$ezeepGroups = ezeepGroups; return ezeepGroups; })
            .finally(() => { $ctrl.$$groupSpin = false; });
    }
});
